
@media (max-width: 2560px) {
    .banner{
        display: flex;
        margin-top: 60px;
        margin-left: auto;
        margin-right: auto;
        color: white;
        &__container{
            display: flex;
            margin-left: auto;
            margin-right: auto;
            &_box{
                // margin-left: -95px;
                &_title{
                    width: 933px;
                    margin-top: 100px;
                    text-transform: uppercase;
                }
                &_text{
                    margin-top: 25px;
                    margin-bottom: 20px;
                }
            }
        }
    }
}


@media (max-width: 1440px) {
    .banner{
        margin-top: 60px;
        margin-left: 60px;
        margin-right: 56px;
        color: white;
        &__container{
            display: flex;
            &_box{
                margin-left: -95px;
                &_title{
                    width: 933px;
                    margin-top: 100px;
                    text-transform: uppercase;
                }
                &_text{
                    margin-top: 25px;
                    margin-bottom: 20px;
                }
            }
        }
    }
}

@media (max-width: 768px) {
    .banner { 
        margin-top: 40px;
        margin-left: 30px;
        margin-right: 30px;
        color: white;
        h1{
            font-size: 24px !important;
        }

        &__container{
            display: flex;
            &_portal{
                img{
                    width: 229px;
                    height: 258px;
                }
            }
            &_box{
                margin-left: -15px;
                width: 350px;
                height: 200px;
                &_title{
                    margin-top: 0px;
                    // width: 308px;
                    // margin-top: 0px;
                    text-transform: uppercase;
                }
                &_text{
                    margin-top: 20px;
                    width: 355px;
                    // margin-left: -95px;
                    margin-bottom: 20px;
                }
            }
        }
    }
}

@media (max-width: 320px) {
    .banner { 
        margin-top: 5px;
        margin-left: 10px;
        margin-right: 10px;
        color: white;

        h1{
            text-align: right;
            font-size: 16px !important;
        }

        &__container{
            // display: flex;
            &_portal{
                img{
                    position: absolute;
                    width: 229px;
                    height: 258px;
                    z-index: -1;
                }
            }
            &_box{
                width: 300px;
                height: 258px;
                align-items: right;
                &_title{
                    margin-top: 16px;
                    width: 234px;
                    text-transform: uppercase;
                    margin-left: auto;
                }
                &_text{
                    display: none;
                    // margin-top: 20px;
                    // width: 355px;
                    // // margin-left: -95px;
                    // margin-bottom: 20px;
                }
            }
        }
    }
}