.button-group {
  // padding-top: 30px;
  display: grid;
  grid-template-columns: repeat(3,247px);     
  gap: 36px;
}


.button {
  background-color: rgb(113,85,198,0.4); 
  color: #FFFFFF;                 
  border: none;                 
  // padding: 10px;                
  width: 247px;                  
  cursor: pointer;              
  transition: background-color 0.3s ease;
  font-family: "Arsenal", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 16px;
  border-radius: 44px;
  width: 247px;
  height: 47px;


  &:hover {
    background-color: lighten($color: #7155C6, $amount: 10); 
  }


  &.active {
    background-color: lighten($color: #7155C6, $amount: 10); 
    color: #fff;                
  }
}

@media (max-width: 768px) {

  .button-group {
    display: grid;
    grid-template-columns: repeat(3,212px);     
    gap: 10px;
  }
  
  
  .button {
    background-color: rgb(113,85,198,0.4); 
    color: #FFFFFF;                 
    border: none;                 
    // padding: 10px;                
    width: 212px;                  
    cursor: pointer;              
    transition: background-color 0.3s ease;
    font-family: "Arsenal", sans-serif;
    font-weight: 400;
    font-style: normal;
    font-size: 16px;
    border-radius: 44px;
    height: 34px;
  
  
    &:hover {
      background-color: lighten($color: #7155C6, $amount: 10); 
    }
  
  
    &.active {
      background-color: lighten($color: #7155C6, $amount: 10); 
      color: #fff;                
    }
  }
  
}